
.j-full,
.j-fluid,
.w-container,
.w-container-c {
    position: relative;
    display: flex;
    box-sizing: border-box;
    gap: 20px;
    z-index: 3;

    &.jgp-large {
        gap: 40px;
    }

    &.jgp-collapse {
        gap: 0;
    }
}
.j-full,
.j-fluid{
    width: 100%;
}
.j-full,
.w-container {   
    //padding: 20px 0; 
}

.j-fluid {
    padding-left: 15px;
    padding-right: 15px;
}

.w-container, .w-container-c, .w-container-noflex {
    position: relative;
    width: 1200px;
    min-width: 1200px;
    margin-left: auto!important;
    margin-right: auto!important;
    padding-left: 35px;
    padding-right: 35px;
}

.w-container-c {
    flex-direction: column;
}

