//mobile ----------------
/* Large desktops and laptops */
@media (min-width: 1200px) {}

/* Portrait tablets and medium desktops */
@media (min-width: 992px) and (max-width: 1199px) {}

/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {}

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {}

/* Landscape phones and smaller */
@media (max-width: 480px) {

    .j-full,
    .j-fluid,
    .w-container {
        min-width: 100%;
        width: 100%;
        // display: grid;
        flex-direction: column;
        padding-right: 20px;
        padding-left: 20px;

        .j-el,
        
        [class^="w-"] {
            width: 100%;
        }

        &.cw-4-2 {
            // display: grid!important;
            // grid-template-columns: auto auto;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 10px;

            .j-el
             {
                // flex-basis: fill;
                flex-basis: calc(50% - 5px);
                // margin-bottom: 20px;
            }
        }

        &.jgp-collapse {
            gap: 0;
        }
    }
    .w-container-noflex {
        min-width: 100%;
        width: 100%;
        padding-right: 20px;
        padding-left: 20px;

        .j-el,
        
        [class^="w-"] {
            width: 100%;
        }

        &.cw-4-2 {
            // display: grid!important;
            // grid-template-columns: auto auto;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 10px;

            .j-el
             {
                // flex-basis: fill;
                flex-basis: calc(50% - 5px);
                // margin-bottom: 20px;
            }
        }

        &.jgp-collapse {
            gap: 0;
        }
    }
}
