.as-s,
.as-c,
.as-st,
.as-s,
.as-e,
.as-b,
.as-a,
// .fs-0,
// .fs-1,
.f-all-c,
.fd-r,
.fd-rr,
.fd-c,
.fd-cr,
.jc-c,
.jc-b,
.jc-s,
.jc-e,
.jc-a,
.jc-ev,
.f-w,
.ai-s,
.ai-e,
.ai-b,
.ai-st,
.ai-c,
.g-05,
.g-1,
.g-2,
.g-3,
.g-4 {
    display: flex;
}

//align self
.as-c {
    align-self: center;
}

.as-st {
    align-self: stretch;
}

.as-s {
    align-self: flex-start;
}

.as-e {
    align-self: flex-end;
}

.as-b {
    align-self: baseline;
}

.as-a {
    align-self: auto;
}

//shrink
.fs-0 {
    flex-shrink: 0;
}

.fs-1 {
    flex-shrink: 1;
}

.fg-1 {
    flex-grow: 1;
}

.fg-0 {
    flex-grow: 0;
}

.f-all-c {
    justify-content: center;
    align-items: center;
}

//direction
.fd-r {
    flex-direction: row;
}

.fd-rr {
    flex-direction: row-reverse;
}

.fd-c {
    flex-direction: column;
}
.fd-cr {
    flex-direction: column-reverse;
}

//justify-content
.jc-c {
    justify-content: center;
}

.jc-b {
    justify-content: space-between;
}

.jc-s {
    justify-content: flex-start;
}

.jc-e {
    justify-content: flex-end;
}

.jc-a {
    justify-content: space-around;
}

.jc-ev {
    justify-content: space-evenly;
}


//align-items
.ai-c {
    align-items: center;
}

.ai-s {
    align-items: flex-start;
}

.ai-e {
    align-items: flex-end;
}

.ai-b {
    align-items: baseline;
}

.ai-st {
    align-items: stretch;
}

.g-0 {
    gap: 0 !important
}




//flex wrap
.f-w {
    flex-wrap: wrap;
}

.fv-full {

    flex: 0 0 auto;
    width: 100%;
}
.f-important{
    display: flex!important;
}


.g-05 {
    gap: 0.5rem
}

.g-1 {
    gap: 1rem
}

.g-2 {
    gap: 2rem
}

.g-3 {
    gap: 3rem
}

.g-4 {
    gap: 3rem
}