html,
body {
  font-size: 16px !important;
  color: #000;
  margin: 0;
}

*{
  margin-block-end: 0;
  margin-block-start: 0;
  box-sizing: border-box;
  color: inherit;
}

a{
  text-decoration: none;
}

ul{
  padding-left: 0;
}


.o-0 {
  opacity: 0;
}

.o-1 {
  opacity: 0.1;
}

.o-2 {
  opacity: 0.2;
}

.o-3 {
  opacity: 0.3;
}

.o-4 {
  opacity: 0.4;
}

.o-5 {
  opacity: 0.5;
}

.o-6 {
  opacity: 0.6;
}

.o-7 {
  opacity: 0.7;
}

.o-8 {
  opacity: 0.8;
}

.o-9 {
  opacity: 0.9;
}

.o-10 {
  opacity: 1;
}

.j-relative,
.pos-r {
  position: relative;
}


.j-hidden,
.jq-hidden {
  display: none;
}